<template>
  <div id="screen">
    <nav class="navbar fixed-top d-block" style="z-index: 999; background-color: rgba(0, 0, 0, 0.5)">
      <div class="row">
        <div class="col-sm-8">
          <div class="d-flex">
            <button type="button" class="btn text-white">
              <i class="bi bi-eye-fill"></i>&nbsp;&nbsp;&nbsp;{{ Object.values(connectedPeers).length }}
            </button>            
          </div>        
        </div>
        <div class="col-sm-4 text-end">
          <button type="button" class="btn text-white" @click="toggleFullScreen()">
            <span v-if="isFullScreen">
              <i class="bi bi-fullscreen-exit"></i>
            </span>
            <span v-else>
              <i class="bi bi-fullscreen"></i>
            </span>
          </button>    
          <button type="button" class="btn text-white" @click="()=> {isMirror = !isMirror}">
            <i class="bi bi-arrow-left-right"></i> Mirror
          </button>    
          <button type="button" class="btn text-white" data-bs-toggle="modal" data-bs-target="#qrcodeModal">
            <i class="bi bi-qr-code"></i> QRCode
          </button> 
        </div>
      </div>   
    </nav>
    <div class="fixed-top d-flex w-100 h-100 align-items-center justify-content-center bg-black" style="z-index: 1;">
      <video class="video" :class="[(isMirror) ? 'mirror' : '']" ref="videoEl"></video>
    </div> 
    
    <div class="modal fade" id="qrcodeModal" tabindex="-1" aria-labelledby="qrcodeModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">        
          <div class="modal-body">
            <div class="row">
              <div class="col">
                ID: {{ myPeerId }}
              </div>
              <div class="col-2 ms-auto text-end">
                <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
              </div>
            </div>
            <div v-if="qrImgSrc">
              <img class="w-100" :src="qrImgSrc"/>
            </div>
          </div>
          <div class="modal-footer justify-content-center border-0">          
            <button type="button" class="btn btn-primary" data-bs-dismiss="modal" aria-label="Close">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { onMounted, ref, inject, computed } from "vue";
import { useStore } from "vuex";
import QRCode from 'qrcode';
const store = useStore()
const videoEl = ref(null)
let peer = null;

const mediaDevices = ref({})
const qrImgSrc = ref(null)

const isMirror = ref(false)
const connectedPeers = ref({})

const isFullScreen = ref(false)

const myPeerId = computed(()=>{
  return store.getters.getPeerId
})

const isMobile = ()=>{
  return /iPhone|iPad|iPod|Android/i.test(window.navigator.userAgent)
}

const loadMyVideo = async()=>{
    let constraint = { video: {width: { exact: 1920 }, height: { exact: 1080 } }, audio: false }
    if(isMobile()){
      constraint.facingMode = { exact: "environment" }
    }
    try{
        const getUserMedia = navigator.mediaDevices.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
        const stream = await getUserMedia(constraint)
        return stream;
    }catch(e) {
        console.error(e)
    }
}

const toggleFullScreen = ()=>{
  const element = document.querySelector('#screen')    
  if (!document.fullscreenElement) {    
    if(element.webkitRequestFullscreen){
      element.webkitRequestFullscreen()
    }else if(element.mozRequestFullScreen){
      element.mozRequestFullScreen()
    }else if(element.msRequestFullscreen){
      element.msRequestFullscreen()
    }else{
      element.requestFullscreen()
    }          
    isFullScreen.value = true
  } else {  
    if (document.webkitCancelFullscreen){
      document.webkitCancelFullscreen()
    }else if(document.mozCancelFullScreen){
      document.mozCancelFullScreen()
    }else if(document.msExitFullscreen){
      document.msExitFullscreen()
    }else{
      document.exitFullscreen()
    }    
    isFullScreen.value = false
  }
}

const onChangeCamera = async(camera)=>{      
  localStorage.setItem('cameraId', camera.deviceId)
  window.location.reload()
}

const loadCameras = async()=>{
  const devices = await navigator.mediaDevices.enumerateDevices()  
  const cameraDevices = devices.filter((item)=> item.kind == 'videoinput')
  cameras.value = cameraDevices
}

onMounted(async ()=>{
  const stream = await loadMyVideo()     
  videoEl.value.srcObject = stream
  videoEl.value.play() 
  qrImgSrc.value = await QRCode.toDataURL(myPeerId.value)
  const peer = new Peer(myPeerId.value)

  peer.on("open", (id) => {              
    
  })

  peer.on('connection', (conn)=>{    
    console.log(conn)
    conn.on('close', ()=>{
      delete connectedPeers.value[conn.peer]
    })
  })

  peer.on("call", (call)=>{    
    call.answer(stream);    
    connectedPeers.value[call.peer] = call
    call.on('close', (event)=>{
      console.log('close')
    })    
  })
})

</script>


<style lang="scss" scoped>
.video{  
  max-width: 95%;  
  &.mirror{
    transform: rotateY(180deg);
    -webkit-transform:rotateY(180deg); /* Safari and Chrome */
    -moz-transform:rotateY(180deg); /* Firefox */
  }
}
</style>